<template>
  <div class="container py-4">
    <!-- ローディング画面-->
    <div v-if="loading" class="loading-container">
      <video autoplay loop muted @loadeddata="onLoadingVideoLoaded">
        <source src="NowLoading.mp4" type="video/mp4">
        Your browser does not support the video tag.
      </video>
    </div>

    <!-- メインコンテンツ -->
    <div v-show="!loading">
      <div v-if="!preloading">
        <!-- ランダム表示エリア -->
        <div class="row">
          <div v-for="work in randomWorks" :key="work.workId" class="col-lg-3 col-md-4 col-sm-6 mb-4">
            <router-link :to="{ name: 'PageWorkPlay', params: { workId: work.workId } }">
              <!-- 動画の場合 -->
              <video v-if="work.type === 'video'" @loadeddata="onItemLoaded" muted loop autoplay class="img-fluid">
                <source :src="work.videoCompressed" type="video/mp4">
              </video>
              <!-- 画像の場合 -->
              <div v-else class="img-slideshow">
                <img v-for="(src, index) in work.pictureSrc"
                  @load="onItemLoaded"
                  :src="src"
                  :key="index"
                  :class="{ active: index === work.activeImageIndex }"
                  class="img-fluid" />
              </div>
            </router-link>
          </div>
        </div>

        <!-- 一覧表示エリア -->
        <table class="table">
          <thead>
            <tr>
              <th>Title</th>
              <th>Genre</th>
              <th>Play</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="work in works" :key="work.workId">
              <td>{{ work.title }}</td>
              <td>{{ work.tags }}</td>
              <td>
                <router-link :to="{ name: 'PageWorkPlay', params: { workId: work.workId } }">play</router-link>
              </td>
            </tr>
          </tbody>
        </table>
      </div>
    </div>
  </div>
</template>

<script>
import { works } from './work.js'

export default {
  name: 'PageWork',
  data() {
    return {
      works, // 作品データ配列
      randomWorks: [], // ランダム表示エリアの作品
      //ローディング関連
      loading: true,
      preloading: true,
      loadedItemsCount: 0, // 読み込まれたアイテム（動画と画像）の数
      totalItems: 0, // 合計アイテム数
    }
  },
  created() {
    this.randomWorks = this.getRandomWorks(12);
    this.loadedItemsCount = 0;
    this.randomWorks.forEach(work => {
      if (work.type === 'picture') {
        this.initSlideshow(work);
      }
    });
  },
  mounted() {
      // ここで作品リストを走査し、合計アイテム数を計算
      this.totalItems = this.randomWorks.reduce((count, work) => {
        return count + (work.type === 'video' ? 1 : work.pictureSrc.length);
      }, 0);
  },
  methods: {
    getRandomWorks(count) {
      let filteredWorks = this.works.filter(work => work.randomUse === true);
      let shuffled = filteredWorks.sort(() => 0.5 - Math.random());
      return shuffled.slice(0, count);
    },
    initSlideshow(work) {
      work.activeImageIndex = 0;
      setInterval(() => {
        work.activeImageIndex = (work.activeImageIndex + 1) % work.pictureSrc.length;
      }, 3000);
    },
    onItemLoaded() {
      this.loadedItemsCount++;
      console.log(this.loadedItemsCount);
      if (this.loadedItemsCount === this.totalItems) {
        this.loading = false; // すべてのアイテムが読み込まれた
      }
    },
    onLoadingVideoLoaded() {
      this.preloading = false; // ローディング動画が読み込まれたら状態を変更
      // メインコンテンツの読み込みをここで開始する（必要に応じて）
    },
  }
}
</script>

<style>
.img-slideshow {
  position: relative;
  width: 100%;
  padding-top: 56.25%; /* 16:9 アスペクト比 */
  overflow: hidden;
}

.img-slideshow img {
  position: absolute;
  top: 50%;
  left: 50%;
  width: auto;
  height: auto;
  max-width: 100%;
  max-height: 100%;
  object-fit: contain; /* 画像をコンテナに収める */
  transform: translate(-50%, -50%);
  opacity: 0;
  transition: opacity 1s ease-in-out;
}

.img-slideshow img.active {
  opacity: 1;
}

.loading-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: white; /* 背景を白に設定 */
  z-index: 1000; /* 必要に応じて */
}

.loading-video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
</style>


