<template>
  <div class="container py-4">
    <!-- 動画の再生 -->
    <div v-if="selectedWork.type === 'video'" class="video-container mb-4">
      <video controls class="img-fluid">
        <source :src="selectedWork.videoSrc" type="video/mp4">
        Your browser does not support the video tag.
      </video>
    </div>

    <!-- 画像の場合 -->
    <div v-else-if="selectedWork.type === 'picture'" class="image-container mb-4">
      <div id="workCarousel" class="carousel slide" data-ride="carousel">
        <!-- カルーセルインジケーター -->
        <div class="carousel-indicators">
          <button v-for="(src, index) in selectedWork.pictureSrc"
                  :key="index"
                  type="button"
                  :data-bs-target="'#workCarousel'"
                  :data-bs-slide-to="index"
                  :class="{ 'active': index === 0 }"
                  :aria-label="'Slide ' + (index + 1)">
          </button>
        </div>

        <div class="carousel-inner">
          <div v-for="(src, index) in selectedWork.pictureSrc"
               :class="['carousel-item', { 'active': index === 0 }]"
               :key="index">
            <img :src="src" class="d-block w-100" alt="...">
          </div>
        </div>

        <button class="carousel-control-prev" type="button" data-bs-target="#workCarousel" data-bs-slide="prev">
          <span class="carousel-control-prev-icon" aria-hidden="true"></span>
          <span class="visually-hidden">Previous</span>
        </button>
        <button class="carousel-control-next" type="button" data-bs-target="#workCarousel" data-bs-slide="next">
          <span class="carousel-control-next-icon" aria-hidden="true"></span>
          <span class="visually-hidden">Next</span>
        </button>
      </div>
    </div>

    <!-- タイトル：左詰め -->
    <h4 class="mb-3">{{ selectedWork.title }}</h4>

    <!-- プロパティ：左右いっぱいに広げる -->
    <div class="d-flex justify-content-between mb-3 properties">
      <p> {{ selectedWork.date }}</p>
      <p> {{ selectedWork.soft }}</p>
      <p v-if="selectedWork.link">
        <a :href="selectedWork.link" target="_blank">
          <i class="fas fa-external-link-alt"></i> <!-- 外部リンクアイコン -->
        </a>
      </p>
      <p v-else class="invisible">Placeholder</p>
    </div>

    <!-- 説明：プロパティの下に -->
    <div class="description mb-4">
      <p>{{ selectedWork.description }}</p>
    </div>

    <!-- 戻るボタン：中央揃え -->
    <div class="d-flex justify-content-center">
      <router-link to="/" class="btn btn-secondary">Back to Works</router-link>
    </div>
  </div>
</template>

<script>
import { works } from './work.js'

export default {
  data() {
    return {
      selectedWork: null,
    };
  },
  name: 'PageWorkPlay',

  created() {
    const workId = this.$route.params.workId;
    this.selectedWork = works.find(work => work.workId === workId);
    console.log(workId);
  },
}
</script>

<style>
  .carousel-item img {
    width: 100%;
    height: auto;
    aspect-ratio: 16 / 9;
    object-fit: contain; /* 画像をコンテナに収める */
  }

  /* カルーセルのコントロール（左右のボタン）の色を変更 */
  .carousel-control-prev-icon,
  .carousel-control-next-icon,
  .carousel-indicators button {
    filter: invert(50%); /* アイコンの色を灰色に変更 */
  }

  .img-fluid {
    box-shadow: 4px 4px 7px rgb(212, 212, 212);
  }

  .propaty {
    margin-bottom: 0px !important;
  }

  .properties .fa-external-link-alt {
    margin-left: 8px; /* アイコンとテキストの間隔 */
  }
</style>

