import { createRouter, createWebHistory } from 'vue-router'

import PageWork from '@/components/Work/PageWork.vue'
import PageWorkPlay from '@/components/Work/PageWorkPlay.vue'
import PageAbout from '@/components/About/PageAbout.vue'
import PageCraft from '@/components/Craft/PageCraft.vue'
import PageProgramming from '@/components/Programming/PageProgramming.vue'
import PageVideo from '@/components/Video/PageVideo.vue'
import PageVSinger from '@/components/VSinger/PageVSinger.vue'


const routes = [
  { path: '/', name: 'PageWork', component: PageWork },
  { path: '/play/:workId', name: 'PageWorkPlay', component: PageWorkPlay },
  { path: '/about', name: 'PageAbout', component: PageAbout },
  { path: '/programming', name: 'PageProgramming', component: PageProgramming },
  { path: '/vsinger', name: 'PageVSinger', component: PageVSinger },
  { path: '/video', name: 'PageVideo', component: PageVideo },
  { path: '/craft', name: 'PageCraft', component: PageCraft }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
